"use client";

import { useState } from "react";
import { Card, CardHeader, CardContent, CardFooter } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { ChevronLeft, ChevronRight } from "lucide-react";
import BeforeAfterSlider from "@/components/BeforeAfterSlider";

export default function BeforeAfterCard({ data }) {
  const [index, setIndex] = useState(0);

  const next = () => setIndex((i) => (i + 1) % data.length);
  const prev = () => setIndex((i) => (i - 1 + data.length) % data.length);

  const t = data[index];

  return (
    <Card className="bg-[#f3f3eb] rounded-3xl p-6 md:p-10 shadow border">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-12 items-center">

        {/* LEFT – Before/After Drag Slider */}
        <BeforeAfterSlider before={t.before} after={t.after} />

        {/* RIGHT – Details */}
        <Card className="rounded-2xl shadow-md border">
          <CardHeader>
            <p className="text-5xl text-gray-300 leading-none">“</p>
          </CardHeader>

          <CardContent className="space-y-3 text-sm text-gray-700">
            <p>
              <strong>Patient Name –</strong> {t.name} <br />
              <strong>Treatment Name –</strong> {t.treatment} <br />
              <strong>Duration –</strong>{" "}
              <span className="font-semibold">{t.duration}</span> <br />
              <strong>Clinic –</strong> {t.clinic}
            </p>

            <p className="text-gray-600 leading-relaxed">
              {t.feedback}
            </p>
          </CardContent>

          <CardFooter className="flex gap-3">
            <Button variant="outline" size="icon" className="rounded-full" onClick={prev}>
              <ChevronLeft className="w-5 h-5" />
            </Button>

            <Button variant="outline" size="icon" className="rounded-full" onClick={next}>
              <ChevronRight className="w-5 h-5" />
            </Button>
          </CardFooter>
        </Card>
      </div>

      <p className="text-center text-xs text-gray-500 mt-3">*Real patient images</p>
    </Card>
  );
}
